import { Transaction } from "@meshsdk/core"
import { CardanoWallet, useWallet } from "@meshsdk/react"
import { AlertCircle, Loader2, ShieldCheck, Wallet } from "lucide-react"
import { useState } from "react"

// YOUR LACE WALLET ADDRESS (This acts as the "Masumi Escrow" for the demo)
const MASUMI_ESCROW_ADDR = "addr_test1qz..." // <--- REPLACE THIS WITH YOUR ACTUAL TESTNET ADDRESS

interface PaymentModalProps {
  isOpen: boolean
  onClose: () => void
  onPaymentComplete: () => void
  fileName?: string
}

export function PaymentModal({ isOpen, onClose, onPaymentComplete, fileName }: PaymentModalProps) {
  const { wallet, connected, name } = useWallet()
  const [isProcessing, setIsProcessing] = useState(false)
  const [txHash, setTxHash] = useState<string | null>(null)

  const handlePay = async () => {
    if (!connected) return

    setIsProcessing(true)
    try {
      console.log("üí∏ Building Transaction...")
      const tx = new Transaction({ initiator: wallet })
      
      // 1. Send 10 ADA (10,000,000 Lovelace)
      tx.sendLovelace(MASUMI_ESCROW_ADDR, "10000000")
      
      // 2. Build & Sign (Triggers Lace Popup)
      const unsignedTx = await tx.build()
      const signedTx = await wallet.signTx(unsignedTx)
      
      // 3. Submit to Blockchain
      const hash = await wallet.submitTx(signedTx)
      console.log("‚úÖ Transaction Sent! Hash:", hash)
      
      setTxHash(hash)
      
      // 4. Wait a moment for UI effect, then proceed
      setTimeout(() => {
        onPaymentComplete()
      }, 2000)

    } catch (error: any) {
      console.error("Payment Failed:", error)
      // In a real app, use a toast. For hackathon, console is fine.
    } finally {
      setIsProcessing(false)
    }
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/90 backdrop-blur-md p-4 animate-in fade-in duration-200">
      <div className="bg-slate-900 border border-white/10 p-8 rounded-3xl w-full max-w-lg shadow-2xl relative overflow-hidden">
        
        {/* Background Glow */}
        <div className="absolute top-0 right-0 w-32 h-32 bg-cyan-500/10 rounded-full blur-[50px] pointer-events-none" />

        <div className="flex justify-between items-start mb-6">
          <div>
            <h2 className="text-2xl font-bold text-white flex items-center gap-2">
              <ShieldCheck className="text-cyan-400" /> Secure Payment
            </h2>
            <p className="text-slate-400 text-sm mt-1">Masumi Protocol Escrow</p>
          </div>
          <button onClick={onClose} className="text-slate-500 hover:text-white transition-colors">
            ‚úï
          </button>
        </div>

        {/* 1. CONNECT WALLET STEP */}
        {!connected ? (
          <div className="text-center py-8">
            <div className="w-16 h-16 bg-slate-800 rounded-full flex items-center justify-center mx-auto mb-4">
              <Wallet className="w-8 h-8 text-slate-400" />
            </div>
            <p className="text-slate-300 mb-6">Connect your CIP-30 Wallet (Lace, Nami) to proceed.</p>
            <div className="flex justify-center">
              <CardanoWallet label="Connect Wallet" />
            </div>
          </div>
        ) : (
          /* 2. PAYMENT STEP */
          <div className="space-y-6">
            <div className="bg-slate-800/50 p-4 rounded-xl border border-white/5">
              <div className="flex justify-between mb-2">
                <span className="text-slate-400">Connected With</span>
                <span className="text-white font-medium capitalize">{name}</span>
              </div>
              <div className="flex justify-between items-center text-lg">
                <span className="text-slate-200">Service Fee</span>
                <span className="font-bold text-cyan-400 text-xl">10.00 ‚Ç≥</span>
              </div>
              {fileName && (
                 <div className="mt-2 text-xs text-slate-500 text-right">File: {fileName}</div>
              )}
            </div>

            {txHash ? (
              <div className="bg-green-500/10 border border-green-500/20 p-4 rounded-xl text-center">
                <p className="text-green-400 font-bold mb-1">Payment Successful!</p>
                <p className="text-xs text-green-300/70 break-all font-mono">{txHash}</p>
              </div>
            ) : (
              <button 
                onClick={handlePay}
                disabled={isProcessing}
                className="w-full py-4 bg-gradient-to-r from-blue-600 to-cyan-600 hover:from-blue-500 hover:to-cyan-500 text-white rounded-xl font-bold text-lg shadow-lg shadow-cyan-500/20 transition-all disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
              >
                {isProcessing ? (
                  <>
                    <Loader2 className="animate-spin" />
                    Signing Transaction...
                  </>
                ) : (
                  "Confirm Payment"
                )}
              </button>
            )}
            
            <p className="text-xs text-slate-500 text-center flex items-center justify-center gap-1">
              <AlertCircle size={12} /> Powered by Cardano Mesh SDK
            </p>
          </div>
        )}
      </div>
    </div>
  )
}